body{
  background-color: #E5E8E8;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#specs{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

#age{
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 30px;
}

#intent{
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-right: 30px;
}

#value-age{
  padding-left: 20px;
  width: 50px;
}

.tick text {
  opacity: 0;
}

.parameter-value text {
  opacity: 0;
}
